.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {

  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    grid-column: 2 / 4;
    width: 100%;
    border-radius: 50%;
    transition: transform $transition-duration;

    &:hover {
      transform: scale(1.3);
      z-index: 1;
    }
  }

  &__link {
    @extend %link-color;
  }

  &__name {
    margin: 0 0 4px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $blue-color;
  }

  &__role {
    margin: 0 0 4px;

    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: $role-info-text-color;
  }

  &__about {
    text-align: center;
  }
}
